@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,200;0,300;0,400;0,600;1,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.preview-page .button-container, .preview-page .timeline-container {
  width: min(500px, 100%);
  margin: auto;
}
.preview-page .timeline-container {
  height: 100%;
}
.preview-page .timeline-container {
  position: relative;
}
.preview-page .timeline-container::after {
  content: "";
  position: absolute;
  background-color: #ff7676;
  width: 4px;
  height: 100%;
  left: calc(50% - 155px);
  top: 35vh;
}
.preview-page .timeline-container .entry-container {
  width: 250px;
  margin-left: auto;
  margin-right: auto;
  position: relative;
}
.preview-page .timeline-container .circle {
  background-color: white;
  border: 3px solid #ff7676;
  height: 50px;
  width: 50px;
  position: absolute;
  z-index: 10;
  border-radius: 25px;
  top: 0px;
  left: calc( 50% - 213px );
}
.preview-page .timeline-container h1 {
  font-family: "Nunito", sans-serif;
  line-height: 100%;
  font-size: 3em;
}
.preview-page .timeline-container h2 {
  font-family: "Nunito", sans-serif;
  line-height: 100%;
  font-size: 2.9em;
  font-weight: 600;
}
.preview-page .timeline-container h3 {
  font-weight: 500;
  color: #ff7676;
}
.preview-page .timeline-container .timeline-content {
  top: calc(35vh - 50px);
  position: relative;
  left: 35px;
}
.preview-page .timeline-container .entry-content {
  position: relative;
  top: 10px;
}
.preview-page .timeline-container .divider {
  height: 400px;
}
.iconContainer {
  height: 40px;
  width: 40px;
}

.link-container {
  height: 20px;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
}

.landing-page h1 {
  position: relative;
  line-height: 1.1;
  font-size: 50px;
  margin-top: 70px;
  font-family: "Nunito", sans-serif;
}

.landing-page h1 span {
  font-family: "Nunito", sans-serif;
}

.landing-page p {
  margin: 20px 0px;
  /* hyphens: auto; */
}

/* positioning */
.intro-block {
  width: 250px;
  margin-left: auto;
  margin-right: auto;
  position: relative;
  left: 35px;
}

.primaryButton, .createForm .timelineForm .finish, .createForm .timelineForm .next, .learnMore, .getStarted {
  margin-right: 5px;
  text-decoration: none;
  width: 10px;
  height: 10px;
  color: black;
  border-radius: 10px;
