  cursor: pointer;
}

.getStarted {
  background-color: #ff7676;
}
.getStarted:active {
  background-color: #ff6d6d;
}

.learnMore {
  color: gray;
}

.landing-page::after {
  content: "";
  position: absolute;
  background-color: #ff7676;
  width: 4px;
  height: 100%;
  left: calc(50% - 155px);
  top: 0px;
}

.intro-block .circle {
  background-color: white;
  border: 3px solid #ff7676;
  height: 50px;
  width: 50px;
  position: absolute;
  z-index: 10;
  border-radius: 25px;
  top: 0px;
  left: calc( 50% - 213px );
}

.createForm h1 {
  font-size: 20px;
  margin: 10px 20px;
  font-family: "Nunito", sans-serif;
}
.createForm h2 {
