  margin-bottom: 50px;
}
.createForm .timelineForm .notes {
  font-size: 0.9em;
  margin: 10px;
  margin-bottom: 15px;
  width: min(470px, 100%);
  margin: auto;
}
.createForm .timelineForm .locationInput {
  height: 150px;
}
.createForm .timelineForm .questionContainer {
  height: auto;
}
.createForm .timelineForm .inputRow {
  margin-top: 20px;
}
.createForm .timelineForm .date-input {
  margin-left: 10px;
  width: 45%;
  float: right;
}
.createForm .timelineForm .text-input {
  width: 60%;
}
.createForm .timelineForm .date-input,
.createForm .timelineForm .text-input {
  font-family: "Montserrat", sans-serif;
  font-weight: 100;
}
.createForm .timelineForm .text-input-wide {
  width: 100%;
}
.createForm .timelineForm .date-input-wide {
  width: 100%;
}
.createForm .timelineForm .addButton, .createForm .timelineForm .deleteButton {
  margin-top: 3px;
  background-color: transparent;
  border: none;
  color: #ff7676;
  cursor: pointer;
  outline: none;
}
.createForm .timelineForm .addButton {
  font-size: 40px;
  font-weight: 200;
  width: 35px;
  height: 35px;
  margin: 25px auto;
  display: block;
  border: 1px solid #ff7676;
  border-radius: calc(17.5px);
  line-height: 35px;
}
.createForm .timelineForm .rangeAndDeleteRow {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  width: 100%;
}
.createForm .timelineForm .dateRange {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}
.createForm .timelineForm .entry-container {
  display: grid;
  grid-template-columns: 55% 30% 15%;
}
.createForm .timelineForm .dialog-content {
  width: 300px;
  height: 300px;
}
.createForm .timelineForm .dialog-button {
  text-transform: "none";
  color: #ff7676;
}
.createForm .timelineForm .finish {
  color: #ff7676;
  margin: auto;
  background-color: white;
  border: 1px solid #ff7676;
  width: 100px;
  height: 40px;
  line-height: 10px;
  clear: both;
  display: block;
}
.createForm .timelineForm .finish:active {
  color: white;
  background-color: #ff7676;
}
.createForm .timelineForm .errorContainer {
  width: min(470px, 100%);
  text-align: center;
  margin: auto;
  margin-top: 30px;
  height: 200px;
}

.primaryButton {
  background-color: #ff7676;
  border: 0px;
  width: auto;
  height: 40px;
  line-height: 10px;
}
.primaryButton:active {
  background-color: #ff6d6d;
}

